@charset "UTF-8";
body {
  width: 100%;
  min-width: 330px;
  padding: 0;
  margin: 0;
  background-color: #ffffff;
  font-family: "Barlow", "Arial", sans-serif; }

.visually-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  white-space: nowrap; }

@font-face {
  font-family: "Barlow";
  src: local("Barlow"), url("../fonts/barlow-regular.woff2") format("woff2"), url("../fonts/barlow-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "Barlow";
  src: local("Barlow"), url("../fonts/barlowbold.woff2") format("woff2"), url("../fonts/barlowbold.woff") format("woff");
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: "Proxima Nova";
  src: local("Proxima Nova"), url("../fonts/proxima-nova-regular.woff2") format("woff2"), url("../fonts/proxima-nova-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "Proxima Nova";
  src: local("Proxima Nova"), url("../fonts/proxima-nova-Bold.woff2") format("woff2"), url("../fonts/proxima-nova-Bold.woff") format("woff");
