.header__icon--profile {
  display: block;
  width: 30px;
  height: 30px;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='26' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 15c2.75 0 5.5-2.21 5.5-6.38 0-4.16-1.83-6.66-5.5-6.66S6.5 5.33 6.5 8.62C6.5 12.8 9.25 15 12 15zm0 0c5.5 0 11 4.39 11 9.45H1c0-4.5 5.5-9.46 11-9.46z' stroke='%23A8A8A8' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
