.header__nav {
  display: block;
  height: 52px;
  border-top: 2px solid #F0F0F4;
  border-bottom: 2px solid #F0F0F4; }

.header__nav-list {
  display: flex;
  list-style: none;
  padding: 0; }

.header__nav-list li {
  display: block;
  text-align: center;
  padding: 0 10px;
  margin: 0 10px; }

.header__nav-list li:first-child {
  padding-left: 0;
  margin-left: 0; }

.header__nav-list li:last-child {
  padding-right: 0;
  margin-right: 0; }

.header__nav-list li a {
  text-decoration: none;
  font-size: 20px;
  color: #48494D; }

.header__nav-list li:hover {
  font-weight: bold;
  border-bottom: 4px solid #FF7931;
  padding-bottom: 12px; }

.slider-wrapper {
  width: 870px;
  overflow: hidden;
  margin-right: 40px;
  margin-left: -10px; }

.slick-track {
  display: flex; }

.slider {
  position: relative; }

.slider .slick-arrow {
  position: absolute;
  top: 50%;
  z-index: 10;
  font-size: 0;
  width: 35px;
  height: 35px;
  border: none; }

.slider .slick-arrow.slick-prev {
  left: 20px;
  background: url("../../img/svg/icon-arrow.svg") 50% 50%/60% no-repeat;
  transform: rotate(-180deg);
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 100%; }

.slider .slick-arrow.slick-prev:hover {
  background-color: rgba(255, 255, 255, 0.8); }

.slider .slick-arrow.slick-next {
  right: 0;
  background: url("../../img/svg/icon-arrow.svg") 50% 50%/60% no-repeat;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 100%; }

.slider .slick-arrow.slick-next:hover {
  background-color: rgba(255, 255, 255, 0.8); }

.slider .slick-dots {
  position: absolute;
  bottom: 4px;
  left: 347px;
  display: flex;
  align-items: center;
  justify-content: center; }

.slider .slick-dots li {
  list-style: none;
  margin: 0 5px; }

.slider .slick-dots li.slick-active button {
  background-color: #fff; }

.slider .slick-dots li button {
  font-size: 0;
  width: 10px;
  height: 10px;
  background-color: transparent;
  border: 1px solid #fff;
  border-radius: 50%;
  padding: 0; }

.banners {
  display: flex;
  flex-direction: row;
  margin-bottom: 82px; }

.banner {
  height: 395px;
  width: 268px;
  margin-top: 20px;
  border: 1px solid #cdcdcd;
  border-radius: 3px; }

.banner__top {
  position: relative;
  width: 100%;
  height: 296px;
  background-color: #ffffff; }
  .banner__top::after {
    position: absolute;
    content: "";
    height: 87px;
    width: 25px;
    left: -1px;
    top: -1px;
    background-image: url("../../img/discount.jpg"); }

.banner__image {
  position: absolute;
  left: 60px;
  top: 50px; }

.banner__price-new {
  position: absolute;
  font-size: 27px;
  color: #FF7931;
  font-weight: 700;
  top: 17px;
  right: 17px; }

.banner__price-old {
  position: absolute;
  font-size: 20px;
  color: #A8A8A8;
  top: 51px;
  right: 25px; }
  .banner__price-old::after {
    position: absolute;
    content: "";
    width: 65px;
    transform: rotate(-12deg);
    border-bottom: 1px solid #A8A8A8;
    top: 10px;
    right: -10px; }

.banner__product {
  position: absolute;
  color: #48494D;
  left: 40px;
  bottom: 13px;
  font-size: 23px;
  letter-spacing: 1px;
  font-weight: 700; }

.banner__down {
  font-family: "Proxima Nova", "Arial", sans-serif;
  text-align: center;
  background-color: #F0F0F4;
  height: 99px; }

.banner__down-timer {
  display: block;
  padding-top: 15px;
  margin-bottom: 15px;
  font-size: 19px;
  color: #48494D; }

.banner__timer {
  position: relative;
  display: flex;
  padding-left: 89px;
  font-size: 20px;
  font-weight: 700; }
  .banner__timer::after {
    position: absolute;
    content: "";
    width: 28px;
    height: 28px;
    background-image: url("../../img/svg/icon-timer.svg");
    left: 46px;
    bottom: -10px; }

.banner__timer p {
  margin: 0; }

.banner__timer-hours {
  position: relative;
  padding-right: 5px; }
  .banner__timer-hours::after {
    position: absolute;
    content: "часов";
    font-size: 10px;
    color: #cdcdcd;
    bottom: -8px;
    left: 0; }

.banner__timer-minutes {
  position: relative;
  padding-right: 5px; }
  .banner__timer-minutes::after {
    position: absolute;
    content: "минут";
    font-size: 10px;
    color: #cdcdcd;
    bottom: -8px;
    left: 10px; }

.banner__timer-seconds {
  position: relative; }
  .banner__timer-seconds::after {
    position: absolute;
    content: "секунд";
    font-size: 10px;
    color: #cdcdcd;
    bottom: -8px;
    left: 9px; }

.search__list {
  display: flex;
  padding: 0;
  margin-bottom: 24px; }

.search__list li {
  list-style: none;
  margin: 0 13px; }

.search__list li:first-child {
  margin-left: 0; }

.search__button {
  text-align: center;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.5px;
  padding: 12px 22px;
  background-color: transparent;
  border: none; }

.search__button--checked {
  font-weight: bold;
  background: #F0F0F4;
  border-radius: 5px; }

.search__input {
  position: relative;
  height: 50px;
  overflow: hidden; }

.search__input-textarea {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.5px;
  color: #656464;
  padding-left: 20px;
  background: #F0F0F4;
  border: none;
  border-radius: 3px; }

.search__submit-button {
  position: absolute;
  right: 0;
  top: 0;
  width: 145px;
  height: 100%;
  background: #FF7931;
  border-radius: 3px;
  border: none;
  color: #fff;
  font-family: "Proxima Nova", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  cursor: pointer; }
